.navbar {
  background-color: white;
  display: flex;
  justify-content: center;
  text-align: center;

  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 50px;

  z-index: 100;
  /* drag 막기 */
  user-select: none;

  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
}

.nav-center {
  width: 1220px;
  display: inline-flex;
  justify-content: space-between;
  align-items: center;
  margin: 0 40px;
}

.nav-wrapper {
  padding: 0;
  display: flex;
  list-style: none;
}

.nav-logo {
  font-size: 16px;
  padding: 6px 10px 8px 10px;
  border-radius: 8px;
  text-decoration: none;
  color: black;
  font-weight: 700;
}

.nav-logo:hover {
  background-color: black;
  color: white;
}

.nav-btn {
  font-size: 14px;
  padding: 6px 10px 8px 10px;
  margin-left: 24px;
  border-radius: 8px;
  text-decoration: none;
  color: black;
}

.nav-btn:hover {
  background-color: rgba(0, 0, 0, 0.05);
}
