.App {
  width: 100vw;
  padding: 0 auto;
  margin: 0 auto;
}

body {
  overflow-x: hidden;
  overflow-y: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

h1 {
  font-size: 24px;
}

h3 {
  font-size: 18px;
}

/* drag */
::selection {
  background: #95c8e4;
  color: #4f97be;
}
