.container-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: #f3f3f3;
  padding: 0 40px !important;
}

.container {
  margin: 80px 0;
  width: 100%;
  max-width: 1224px;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 30px 10px;
}
