.big-card {
  /* background-color: white; */
  grid-column: 1 / 4;
  border-radius: 32px;
  padding: 12px 12px 6px 12px;
  transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s;
  user-select: none;
}

.big-card:hover {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: rgba(176, 176, 176, 0.05) 0px 8px 24px;
  transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.3s;
}

.big-card .thumbnail {
  background-color: black;
  height: 380px;
  border-radius: 24px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;

  background-image: url("../imgs/1.png");
}

.big-card .text-wrapper {
  margin: 14px 16px 14px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.big-card .text-wrapper h3 {
  font-size: 18px;
}

.big-card .text-wrapper div {
  display: flex;
  align-items: center;
}

.big-card .text-wrapper div p {
  font-size: 16px;
  opacity: 0.6;
  font-weight: 300;
}

.big-card .text-wrapper .tag {
  margin-left: 20px;
  padding: 2px 6px 4px 6px;
  border-radius: 4px;
  background-color: #95c8e4;
  color: #6baacc;
  font-weight: bold;
  font-size: 14px;
}
