.small-card {
  /* background-color: white; */
  border-radius: 32px;
  padding: 12px 12px 5px 12px;
  transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.2s;
  user-select: none;
}

.small-card:hover {
  background-color: rgba(255, 255, 255, 0.5);
  box-shadow: rgba(176, 176, 176, 0.1) 0px 8px 24px;
  /* box-shadow: rgba(171, 171, 171, 0.03) 0px 2px 8px 0px; */
  transition: cubic-bezier(0.785, 0.135, 0.15, 0.86) 0.2s;
}

.small-card .thumbnail {
  background-color: white;
  height: 260px;
  border-radius: 24px;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  padding: 17px;
}

.small-card .text-wrapper {
  margin: 14px 16px 14px 16px;
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.small-card .text-wrapper h3 {
  font-size: 18px;
  font-weight: bold;
  padding: 0 !;
}

.small-card .text-wrapper .tag-wrapper {
  display: flex;
}

.small-card .text-wrapper .tag-wrapper .years-tag {
  margin-left: 4px;
  padding: 2px 6px 4px 6px;
  border-radius: 4px;
  background-color: #d9d9d9;
  color: #bdbdbd;
  font-weight: bold;
  font-size: 14px;
}

.small-card .text-wrapper .tag-wrapper .UX-tag {
  margin-left: 4px;
  padding: 2px 6px 4px 6px;
  border-radius: 4px;
