  color: #599fc5;
  font-weight: bold;
  font-size: 14px;
}

.small-card .text-wrapper .tag-wrapper .BX-tag {
  margin-left: 4px;
  padding: 2px 6px 4px 6px;
  border-radius: 4px;
  background-color: #cec0e0;
  color: #a88ace;
  font-weight: bold;
  font-size: 14px;
}

.small-card .text-wrapper .tag-wrapper .motion-tag {
  margin-left: 4px;
