.behance-badge {
  background-color: rgba(56, 56, 56, 0.2);
  /* border: 1px solid rgba(255, 255, 255, 0.1); */
  backdrop-filter: blur(30px);
  padding: 18px 20px;
  border-radius: 12px;
  color: white;
  font-size: 18px;
  font-weight: bold;
  display: flex;
  justify-content: space-between;
  /* animation */
  animation: badge-in 0.5s;
}

/* animation */
@keyframes badge-in {
  0% {
    opacity: 0;
    transform: translateY(-15%);
  }
  100% {
    opacity: 1;
    transform: translateY(0%);
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
